<template>
  <!-- 确认提示框组件 -->
  <transition name="confirm_tran">
    <div class="confirm" v-if="isShow">
      <div class="confirm_context">
        <p class="confirm_tips">{{ text.type }}</p>
        <p class="confirm_msg">{{ text.msg }}</p>
        <div class="confirm_btn">
          <span @click="close()" v-if="text.btn.no">{{ text.btn.no }}</span>
          <span @click="ok()" v-if="text.btn.ok">{{ text.btn.ok }}</span>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
export default {
  data() {
    return {
      isShow: true,
      text: {
        type: "提示",
        msg: "确定删除此条信息？",
        btn: {
          ok: "确定",
          no: "取消",
        },
      },
    };
  },
  methods: {
    close() {
      console.log("关闭");
    },
    ok() {
      console.log("确定");
    },
  },
};
</script>
<style>
.confirm_tran-enter-active,
.confirm_tran-leave-active {
  transition: all 0.5s ease;
}
.confirm_tran-enter,
.confirm_tran-leave-to {
  opacity: 0;
}
/* 外层遮罩层 */
.confirm {
  background-color: rgba(0, 0, 0, 0.3);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  z-index: 999;
}
.confirm_context {
  width: 300px;
  background-color: white;
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translate(-50%);
  border-radius: 5px;
  padding: 10px;
  box-sizing: border-box;
}

.confirm_tips {
  text-align: center;
  line-height: 30px;
  font-size: 18px;
  font-weight: 700;
}
.confirm_msg {
  margin: 10px 0;
  color: rgb(122, 122, 122);
  line-height: 20px;
  text-align: center;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  overflow: hidden;
}
.confirm_btn {
  overflow: hidden;
}
.confirm_btn span {
  font-size: 14px;
  cursor: pointer;
  float: right;
  height: 30px;
  width: 50px;
  text-align: center;
  line-height: 30px;
  margin-left: 10px;
  border-radius: 5px;
  color: rgb(122, 122, 122);
  transition: 0.3s;
}
.confirm_btn span:hover {
  color: #fff;
  background: rgb(0, 60, 255);
}
</style>
