import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
Vue.config.productionTip = false
// markdown样式
import 'github-markdown-css'
// 代码高亮devibeans   swh自定义颜色
import './assets/css/swh.css'

// 引入md文章组件列表
import  { mdArticle } from './article/index'
Vue.use(mdArticle)

// 全局组件
import SupTopUI from './components/component_module'
Vue.use(SupTopUI);

//确认提示框的绑定
import MyConfirm from './components/component_module/confirm//confirm.js'
Vue.prototype.$confirm = MyConfirm;


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
